import { FindDPI } from '../../../lib/finddpi/FindDpi';
import { SheetUtils } from '../../../utils/SheetUtils';

/**
 * È´òÂ∫¶Âçï‰Ωç
 */
class HeightUnit {

  /**
   * HeightUnit
   * @param dpi
   */
  constructor({
    dpi,
  } = {}) {
    if (SheetUtils.isDef(dpi)) {
      this.dpi = dpi;
    } else {
      this.dpi = FindDPI();
    }
  }

  /**
   * Ëé∑ÂèñDPI
   * @returns {*}
   */
  getDpi() {
    return this.dpi;
  }

  /**
   * Á£ÖËΩ¨ÂÉèÁ¥†
   */
  getPixel(pt) {
    return pt * this.dpi / 72;
  }

  /**
   * ÂÉèÁ¥†ËΩ¨Á£Ö
   */
  getPoint(px) {
    return 72 * px / this.dpi;
  }

}

export {
  HeightUnit,
};
