import { XDraw } from '../../../draw/XDraw';
import { SheetUtils } from '../../../utils/SheetUtils';

/**
 * WideUnit
 */
class WideUnit {

  /**
   * WideUnit
   * @param table
   * @param unit
   * @param fontName
   * @param fontSize
   * @param fontBold
   * @param fontItalic
   */
  constructor({
    table = null,
    unit = null,
    fontName = 'Arial',
    fontSize = 10,
    fontBold = false,
    fontItalic = false,
  } = {}) {
    if (SheetUtils.isDef(unit)) {
      this.unit = unit;
    } else {
      const { draw, heightUnit } = table;
      // Â≠ó‰ΩìÂÉèÁ¥†
      const fontPixel = heightUnit.getPixel(fontSize);
      const srcPixel = XDraw.srcPx(fontPixel);
      const sizePixel = XDraw.trunc(srcPixel);
      // Â≠ó‰ΩìÊ†∑Âºè
      const bold = `${fontBold ? 'bold' : ''}`;
      const italic = `${fontItalic ? 'italic' : ''}`;
      const style = `${italic} ${bold} ${sizePixel}px ${fontName}`;
      // Â∫¶ÈáèÂ≠ó‰Ωì
      draw.save();
      draw.attr({
        font: style.trim(),
      });
      let unit = 0;
      for (let i = 0; i < 10; i++) {
        const { width } = draw.measureText(i.toString());
        if (width > unit) {
          unit = width;
        }
      }
      draw.restore();
      this.unit = unit;
      // Firefox Â•ΩÂÉè‰∏çÂáÜÁ°Æüòì
      const { type } = SheetUtils.getExplorerInfo();
      switch (type) {
        case 'Firefox':
          this.unit = XDraw.trunc(unit) + 0.22;
          break;
        case 'Chrome':
          this.unit = unit;
          break;
      }
    }
  }

  /**
   * Ëé∑ÂèñÂΩìÂâçÊµãÈáèÂçï‰Ωç
   * @returns {*}
   */
  getUnit() {
    return this.unit;
  }

  /**
   * Ëé∑ÂèñÂ≠óÁ¨¶ËøêË°åÊó∂ÂÉèÁ¥†
   * @param nb Â≠óÁ¨¶Êï∞
   */
  getNumberPixel(nb) {
    return this.getWidePixel(this.getNumberWide(nb));
  }

  /**
   * Ëé∑ÂèñÂÆΩÂ∫¶ËøêË°åÊó∂ÂÉèÁ¥†
   * @param wide ÂàóÂÆΩ
   * @returns {number}
   * @see https://docs.microsoft.com/en-us/dotnet/api/documentformat.openxml.spreadsheet.column?redirectedfrom=MSDN&view=openxml-2.8.1
   */
  getWidePixel(wide) {
    return XDraw.trunc(((256 * wide + XDraw.trunc(128 / this.unit)) / 256) * this.unit);
  }

  /**
   * Ëé∑ÂèñÂ≠óÁ¨¶ÂÆΩÂ∫¶
   * @param nb Â≠óÁ¨¶Êï∞
   * @see https://docs.microsoft.com/en-us/dotnet/api/documentformat.openxml.spreadsheet.column?redirectedfrom=MSDN&view=openxml-2.8.1
   */
  getNumberWide(nb) {
    return XDraw.trunc([nb * this.unit + 5] / this.unit * 256) / 256;
  }

  /**
   * Ëé∑ÂèñÂÉèÁ¥†ÂØπÂ∫îÁöÑÂ≠óÁ¨¶Êï∞
   * @param pixel
   * @returns {number}
   * @see https://docs.microsoft.com/en-us/dotnet/api/documentformat.openxml.spreadsheet.column?redirectedfrom=MSDN&view=openxml-2.8.1
   */
  getPixelNumber(pixel) {
    return XDraw.trunc((pixel - 5) / this.unit * 100 + 0.5) / 100;
  }

}

export {
  WideUnit,
};
